.App {
  text-align: center;
}

.img-main-game {
  width: 1000px;
  height: 100%;
}

.container {
  display: flex;
  justify-content: center;
}

.game-chars {
  text-align: left;
  padding-left: 2rem;
  width: 250px;
  font: 1rem;
}

.game-char-found {
  color: rgb(4, 169, 54);
  font-weight: bold;
}

.game-chars li {
  padding: 5px 0;
}

.img-container {
  position: relative
}

.game-msg {
  height: 50px;
  width: 60%;
  font-size: 2rem;
  font-style: bold;
  color: rgb(255, 255, 255);
  text-shadow: 2px 1px rgb(61, 62, 63);
  z-index: 1;
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translate(-50%, -50%);
}


.current-player-scoreboard {
  gap: 100px;
}

.current-player-scoreboard p {
  width: 200px;
  gap: 0;
}